<!-- 修改性别 -->
<template>
  <div class="genderBox">
    <van-picker
      show-toolbar
      :columns="columns"
      @cancel="$emit('genderBoxClose')"
      @confirm="onConfirm"
      :default-index='value'
    />
  </div>
</template>

<script>
import { setUserInfo } from '@/api/user.js'
export default {
  name: 'genderBox',
  components: {},
  props: {
    // 接收父组件传过来的性别信息
    value: {
      type: [String, Number],
      required: true
    }
  },
  data () {
    return {
      columns: ['男', '女'] // 数据
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    // 确认事件
    async onConfirm (value) {
      // 获取选择的下标发送请求
      const valuesIndex = this.columns.indexOf(value)
      console.log(valuesIndex)
      await setUserInfo({
        gender: valuesIndex
      })
      // 关闭弹出层
      this.$emit('genderBoxClose')
      // 通过v-model修改视图数据
      this.$emit('input', valuesIndex)
    }
  }
}
</script>

