<!-- 修改昵称 -->
<template>
  <div class="nameBox">
    <!--顶部-->
    <navbar class="nameBoxTop">
      <i class="icon icon-guanbi" slot="left" @click="$emit('nameBoxClose')"></i>
      <p slot="title">修改昵称</p>
      <p slot="right" @click='setName'>保存</p>
    </navbar>
    <div class="nameText">
      <van-field
        v-model="message"
        rows="2"
        autosize
        type="textarea"
        maxlength="7"
        placeholder="请输入昵称"
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import navbar from '../../../components/navbar'
// 请求修改用户信息
import { setUserInfo } from '@/api/user'
export default {
  name: 'nameBox',
  data () {
    return {
      message: '' // 输入框绑定的内容
    }
  },
  props: {
    // oldName: {
    //   type: String,
    //   required: true
    // }
    value: {
      type: String,
      required: true
    }
  },
  components: {
    navbar
  },
  methods: {
    // 修改昵称
    async setName () {
      try {
        // 加载提示
        this.$toast.loading({
          message: '修改中...',
          forbidClick: true,
          duration: 0
        })
        // 请求修改昵称
        await setUserInfo({
          name: this.message.String
        })
        // 成功提示
        this.$toast.success('修改成功')
        // 关闭弹出层
        // this.$emit('nameBoxCloses', this.message)
        this.$emit('nameBoxClose') // 关闭弹出层
        this.$emit('input', this.message) // 更改当前显示name
      } catch (err) {
        if (err && err.response && err.response.status === 409) {
          // 失败提示
          this.$toast.fail('昵称已存在')
        } else {
          this.$toast.fail('修改失败')
        }
      }
    }
  },
  created () {
    // 初始化输入框
    this.message = this.value
  }
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .nameBoxTop *{
    color: #fff;
  }
  .nameText{
    padding: 12px;
  }
</style>
