<template>
  <div class="photoBox">
    <img :src="photo" alt="" ref='images' class='updata-img'>
    <div>
      <p @click="$emit('closePhotoBox')">取消</p>
      <p @click='getPhoto'>确定</p>
    </div>
  </div>
</template>

<script>
import { setPhoto } from '@/api/user.js'
// TODO 引入图像裁切的样式和js文件
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
export default {
  name: 'photoBox',
  components: {},
  props: {
    // 上传的文件对象
    file: {
      // type: [String, Object],
      required: true
    }
  },
  data () {
    return {
      // 文件对象需要转换为url才能动态绑定到img标签
      photo: window.URL.createObjectURL(this.file)
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {
    // TODO 获取需要裁切的图片DOM
    const image = this.$refs.images
    // 图片限制
    const cropper = new Cropper(image, {
      aspectRatio: 1, // 图像比例约束
      viewMode: 1,
      dragMode: 'move',
      cropBoxMovable: false, // 禁止移动裁切框
      cropBoxResizable: false, // 禁止裁切框缩放
      background: false, // 无背景
      movable: true, // 图像是否可以拖动
      highlight: true
    })
    console.log(cropper)
  },
  methods: {
    async getPhoto () {
      try {
        // loding
        this.$toast.loading({
          message: '修改中...',
          forbidClick: true,
          duration: 0
        })

        const fd = new FormData() // 新建一个FromData对象
        fd.append('photo', this.photo) // 把参数添加进FromData对象里 再发送请求，带上这个fd对象就可以了

        // 请求修改头像
        await setPhoto(fd)
        this.$emit('setPhoto', this.photo) // 修改视图
        this.$toast.success('修改成功')
      } catch (err) {
        // console.log(err)
        this.$toast.fail('修改失败')
      }
      this.$emit('closePhotoBox') // 关闭弹出层
    }
  }
}
</script>

<style scoped>
.updata-img{
  background-color: #666
}
.photoBox>div{
  position:fixed;
  bottom: 0;
  padding: 0 12px;
  width: 100%;
  box-sizing:border-box;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center
}
.photoBox>img{
  display: block;
  max-width: 100%
}
.photoBox>div>*{
  font-size: 14px;
  color: #fff;
}
</style>
