<template>
  <div>
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title="选择出生日期"
      :min-date="minDate"
      :max-date="maxDate"
      @confirm="isOld"
      @cancel="$emit('closeOldBox')"
    />
  </div>
</template>

<script>
import { setUserInfo } from '@/api/user.js'
export default {
  name: 'oldBox',
  props: {
    // 生日数据
    value: {
      type: String,
      require: true
    }
  },
  data () {
    return {
      minDate: new Date(0, 0, 1), // 最小日期
      maxDate: new Date(), // 最大日期 （这里是当前日期）
      currentDate: new Date(this.value) // 选中的日期
    }
  },
  methods: {
    // value是当前选中的时间对象
    async isOld (value) {
      const times = `${value.getFullYear()}-${value.getMonth() + 1}-${value.getDate()}` // 把new time()时间对象处理成字符串
      // console.log(times)
      // loding
      try {
        this.$toast.loading({
          message: '修改中...',
          forbidClick: true,
          duration: 0
        })
        await setUserInfo({
          birthday: times
        })
        // 成功提示框
        this.$toast.success('修改成功')
        this.$emit('input', times)
        this.$emit('closeOldBox')
      } catch (err) {
        this.$toast.fail('修改失败')
      }
    }
  }
}
</script>

