<template>
  <div class="setmy-box">
    <navbar>
      <i slot="left" class="icon icon-youjiantou setmy-title" @click="$router.back()"></i>
      <p slot="title" class='setmy-title'>个人资料</p>
    </navbar>
    <van-cell title="头像" is-link @click='$refs.files.click()'>
      <van-image
        round
        width='35px'
        height='35px'
        :src="userProfileData.data.photo"
        slot="default"
        fit="cover"
      />
    </van-cell>
    <van-cell title="昵称" is-link  :value="userProfileData.data.name" @click="isNameBox = true"/>
    <van-cell
      title="性别"
      is-link
      :value="userProfileData.data.gender === 1 ? '女' : '男'"
      @click='isGenderBox = true'
    />
    <van-cell title="生日" is-link :value="userProfileData.data.birthday" @click="isOldBox = true"/>

    <!-- 修改头像,使用了input上传文件，hidden隐藏了该input标签，然后通过店家头像栏来触发该标签的点击事件 -->
    <input
      type="file"
      hidden
      ref='files'
      accept='image/*'
      @change='onChange'
    >
    <!-- 修改头像 弹出层 -->
    <van-popup position="bottom" v-model="isPhotoBox" :style="{ height: '100%' }" >
      <photoBox
        :file='file'
        @closePhotoBox='isPhotoBox = false'
        @setPhoto="userProfileData.data.photo = $event"
      ></photoBox>
    </van-popup>
    <!-- 修改姓名 -->
    <van-popup position="bottom" :style="{ height: '100%' }" v-model="isNameBox">
      <nameBox v-if="isNameBox"
        @nameBoxClose="isNameBox = false"
        v-model="userProfileData.data.name"
      ></nameBox>
    </van-popup>
    <!-- 修改性别 -->
    <van-popup position="bottom" v-model="isGenderBox">
      <genderBox
        v-if='isGenderBox'
        @genderBoxClose='isGenderBox=false'
        v-model='userProfileData.data.gender'>
      </genderBox>
    </van-popup>
    <!-- 修改生日 -->
    <van-popup position="bottom" v-model="isOldBox">
      <old-box
        v-if="isOldBox"
        v-model="userProfileData.data.birthday"
        @closeOldBox="isOldBox=false"
      >
      </old-box>
    </van-popup>
  </div>
</template>

<script>
// 顶部导航栏组件
import navbar from '../../../components/navbar'
import { userProfile } from '../../../api/user'
import oldBox from './oldBox'
import nameBox from './nameBox'
import genderBox from './genderBox'
import photoBox from './photoBox'
export default {
  name: 'setMy',
  data () {
    return {
      userProfileData: '', // 存用户信息
      isNameBox: false, // 控制修改姓名显示
      isGenderBox: false, // 控制修改性别显示
      isOldBox: false, // 控制生日显示
      isPhotoBox: false, // 控制头像弹出层
      file: '' // 存放选中的本地图片
    }
  },
  components: {
    navbar,
    nameBox,
    genderBox,
    oldBox,
    photoBox
  },
  created () {
    this.getUserProfile() // 获取用户资料
  },
  methods: {
    // 获取用户资料
    async getUserProfile () {
      const { data } = await userProfile()
      this.userProfileData = data
    },
    // 头像上传时触发该事件(元素改变事件)
    onChange (event) {
      // console.log(event.target.files[0]) // 获取上传的文件内容
      this.file = event.target.files[0] // 上传的文件对象
      this.isPhotoBox = true // 显示弹出层
      // 每次都清空该input标签中上传的文件，不然重复文件不会每次触发该事件
      event.target.value = ''
      // this.$refs.files.value ''
    }
  }
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .van-popup{
    background-color: #f8f8f8;
  }
  .setmy-title{
    color: #fff;
  }
  .van-cell{
    display: flex;
    align-items: center;
  }
  .van-popup{
    background-color: #666
  }
</style>
